<template>
<div class="like">
    <van-nav-bar title="我的收藏" left-text="返回" left-arrow @click-left="onClickLeft" />
    <div class="like-box">

        <ul>
            <li class="item border-bottom-1px" v-for="(item,index) in likeList" :key="item.id" @click="showDetailHandle(item.pid)">
                <div class="like-img">
                    <img class="img-layout" :src="item.smallImg" alt="">
                </div>
                <div class="like-info">
                    <h3 class="name">{{item.name}}</h3>
                    <p class="price">￥{{item.price}}</p>
                </div>
                <div class="like-rate" @click.stop="cancelLike(item.pid,index)">
                    <i class="iconfont icon-xin" />
                </div>
            </li>
        </ul>
    </div>

    <div class="empty-box" v-if="likeList.length === 0">
        <van-empty class="custom-image" image="https://img.yzcdn.cn/vant/custom-empty-image.png" description="收藏夹空空如也，去逛逛吧~">
            <button class="btn" @click="showMenuHandle">去逛一逛 ~</button>
        </van-empty>
    </div>
</div>
</template>

<script>
import '../assets/scss/like.scss'
import {
    mapGetters
} from 'vuex'
import {
    utils
} from '../assets/js/utils'

export default {
    data() {
        return {
            likeList: []
        }
    },
    created() {
        this.getLikeDate();
    },
    methods: {
		//
        showMenuHandle() {
            this.$router.push({name: 'Menu'})
        },
        // 查看商品详情
        showDetailHandle(pid) {
            this.$router.push({
                name: 'Detail',
                params: {
                    id: pid
                }
            })
        },
        // 取消收藏
        cancelLike(pid, index) {
            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })

                return;
            }

            let data = utils.queryString({
                appkey: this.appkey,
                tokenString: this.getToken,
                pid
            })

            this.$toast({
                type: 'loading',
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
                duration: 0
            })

            this.axios({
                    method: 'POST',
                    url: '/notlike',
                    data
                })
                .then(res => {
                    this.$toast.clear();
                    console.log(res);
                    this.$toast(res.data.msg);

                    if (res.data.code === 900) {
                        this.likeList.splice(index, 1);
                    }

                })
                .catch(err => {
                    this.$toast.clear();
                    console.log(err);
                })
        },
        // 获取所有收藏商品数据
        getLikeDate() {
            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })

                return;
            }

            let params = {
                appkey: this.appkey,
                tokenString: this.getToken
            }

            this.$toast({
                type: 'loading',
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
                duration: 0
            })

            this.axios({
                    method: 'GET',
                    url: '/findAllLike',
                    params
                })
                .then(res => {
                    this.$toast.clear();
                    console.log(res);
                    if (res.data.code === 2000) {
                        this.likeList = res.data.result;
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                    console.log(err);
                })
        },
        onClickLeft() {
            this.$router.go(-1);
        }
    },
    computed: {
        ...mapGetters(['getToken'])
    }
}
</script>
